	module.exports = {"data":{"markdownRemark":{"html":"<p>到资料室为文化主权的文章再查一些资料，查到了荣苏克蔡)的著作《文化与国际关系》。可能是直接讨论文化与国际关系的唯一的一本书。另外有罗斯诺的《世界的混乱变革和继承的理论》、詹姆斯梅耶尔的《民族主义和国际社会》。一些观念是有启发的。如谈到的\"文化真性)对民族主义的作用，以及\"文化帝国主义\"的概念。</p>\n<p>不少作者认为，我们正在走向一种世界范围内的单一样式的文化，取代\"殖民化\")的将是\"可口可乐化。它不言而喻地意味着文化统一了，但同时又更贫乏了。在亚洲、非洲、欧洲丰富的传统中如此之多的多样性都消失了。荷兰作家皮尔森在《文化战略》一书中就是这个观点。世界的文化应该是多样化的，不能是单一的和单调的。在目前全球一体化的潮流中，怎样保持文化的多样性，的确是一个令人关注的问题。这个问题不仅发生在发达国家和发展中国家之间，也发生在发达国家之间，如法国最近作出决定，要\"保护法语\"，主要是防止美国文化对法语的侵袭;欧洲影视界多次聚会，讨论抵制好莱坞的侵略;等等。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0058.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0058.md","frontmatter":{"title":"3月14日 星期一"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0058.md absPath of file >>> MarkdownRemark"}}

